.NewArrivals {
    margin: 100px 0;
}
.product {
    background: #fff;
    padding: 20px;
    position: relative;
    border-radius: 8px;
    box-shadow: rgb(3 0 71 / 9%) 0px 1px 3px;
    margin: 10px;
}


.grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 30px;
}

.NewArrivals .box img {
    width: 100%;
    height: 100%;
}

.track-main .owl-nav .owl-prev{
display: flex !important;
}

.track-main .owl-nav .owl-prev span {
    font-size: 50px;
    position: relative;
    bottom: 140px;
}

.track-main .owl-nav .owl-next span {
    font-size: 50px;
    position: relative;
    bottom: 205px;
    left: 1682px;
}

.track-main .owl-nav .owl-next{
    display: flex !important;
}